import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { SignComponent } from './sign/sign.component';
import { SettingComponent } from './setting/setting.component';
import { RegisterComponent } from './register/register.component';
import { ProfileComponent } from './profile/profile.component';
import { ArticleDetailComponent } from './article-detail/article-detail.component';
import { EditorComponent } from './editor/editor.component';
import { AuthGuastGuard } from './services/auth-guast.guard';
import { NotFoundComponent } from './not-found/not-found.component';


const routes: Routes = [
    { path: '', component: HomeComponent },
    { path: 'login', component: SignComponent },
    {
        path: 'setting', canActivate: [AuthGuastGuard], children: [
            { path: '', component: SettingComponent }
        ]
    },
    { path: 'register', component: RegisterComponent },
    { path: ':userName', component: ProfileComponent },
    { path: 'article/:slug', component: ArticleDetailComponent },
    {
        path: 'new/editor', canActivate: [AuthGuastGuard], children: [
            { path: '', component: EditorComponent }
        ]
    },
    {
        path: 'editor/:slug', canActivate: [AuthGuastGuard], children: [
            { path: '', component: EditorComponent }
        ]
    },
    { path: '**', component: NotFoundComponent }
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule]
})
export class AppRoutingModule { }
