import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
export interface User {
  email: string,
  password: string
}

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  isLogin: boolean = false;
  name: string = '';
  constructor(private http: HttpClient, private router: Router) {
    if (localStorage.getItem('token')) {
      this.isLogin = true;
      this.name = localStorage.getItem('name');
    }
  }

  login(user: User): Observable<any> {
    return this.http.post('https://conduit.productionready.io/api/users/login', { user: user });
  }

  loginSuccess(userData): any {
    this.isLogin = true;
    this.name = userData['user'].username;
    localStorage.setItem('token', userData['user'].token);
    localStorage.setItem('name', userData['user'].username);
    localStorage.setItem('image', userData['user'].image);
    localStorage.setItem('isLogin', JSON.stringify(this.isLogin));
    this.router.navigate(['']);
  }

  logOut() {
    this.isLogin = false;
    this.name = '';
    localStorage.removeItem('token');
    localStorage.removeItem('name');
    localStorage.removeItem('isLogin');
    this.router.navigate(['login']);
  }

  checkLoginNavigate() {
    if (!this.isLogin) {
      this.router.navigateByUrl("/login");
    }
    return this.isLogin;
  }
}
