import { Component, OnInit } from '@angular/core';
import { ArticleService } from '../services/article.service';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';
import { PaperService } from '../services/paper.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  listGlobalArticle;
  listArticle;
  listTabs;
  listYouFeed;
  listPopularTags;
  toggleTab = false;
  tab = 2;
  favorited;
  tagList;
  imgNull = 'https://static.productionready.io/images/smiley-cyrus.jpg';
  namePopularTag = '';
  //pages
  pager: any = {};
  total1;
  total2;
  total3;
  // paged items
  pagedItems: any[];
  constructor(private articleService: ArticleService, private authService: AuthService, private router: Router, private pagerService: PaperService) {
  }

  ngOnInit() {
    this.articleService.getGlobalArticle(this.authService.isLogin, 0).subscribe(data => {
      this.listGlobalArticle = data['articles'];
      this.listArticle = data['articles'];
      this.total2 = data['articlesCount'];
      this.setPage(1);
    })

    this.articleService.getTabs().subscribe(data => {
      this.listTabs = data['tags'];
    })
  }

  clickYourFeed() {
    this.tab = 1;
    this.toggleTab = false;
    let name = localStorage.getItem('name');
    this.articleService.getHomeArticle(0).subscribe((data) => {
      this.listArticle = data['articles'];
      this.listYouFeed = data['articles'];
      this.total1 = data['articlesCount'];
      this.setPage(1);
    })
  }

  clickGlobalFeed() {
    this.tab = 2;
    this.toggleTab = false;
    this.articleService.getGlobalArticle(this.authService.isLogin, 0).subscribe(data => {
      this.listGlobalArticle = data['articles'];
      this.listArticle = data['articles'];
      this.total2 = data['articlesCount'];
      this.setPage(1);
    })
  }

  clickPopularTags(nameTab) {
    this.namePopularTag = nameTab;
    this.tab = 3;
    this.toggleTab = true;
    this.articleService.getTabArticle(nameTab, this.authService.isLogin, 0).subscribe((data) => {
      this.listArticle = data['articles'];
      this.listPopularTags = data['articles'];
      this.total3 = data['articlesCount'];
      this.setPage(1);
    })
  }

  toggleFavorite(slug, favorited, i) {
    if (this.authService.isLogin) {
      if (favorited == false) {
        this.articleService.postFavoriteArticle(slug).subscribe(data => {
          this.listArticle[i] = data['article'];
        })
      } else if (favorited == true) {
        this.articleService.deleteFavoriteArticle(slug).subscribe(data => {
          this.listArticle[i] = data['article'];
        })
      }
    } else {
      this.router.navigate(['login'])
    }
  }
  // phan trang
  getDataPage(offset) {
    let offSetPage = offset * 10;
    switch (this.tab) {
      case 1:
        let name = localStorage.getItem('name'); 
        this.articleService.getHomeArticle(offSetPage).subscribe((data) => {
        this.listArticle = data['articles'];
      });
        break;
      case 2: this.articleService.getGlobalArticle(this.authService.isLogin, offSetPage).subscribe(data => {
        this.listArticle = data['articles'];
      });
        break;
      case 3: this.articleService.getTabArticle(this.namePopularTag, this.authService.isLogin, offSetPage).subscribe((data) => {
        this.listArticle = data['articles'];
      });
        break;
    }
  }
  setPage(page: number) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    // get pager object from service
    switch (this.tab) {
      case 1: this.pager = this.pagerService.getPager(this.total1, page, 10);
        this.getDataPage(page - 1);
        break;
      case 2: this.pager = this.pagerService.getPager(this.total2, page, 10);
        this.getDataPage(page - 1);
        break;
      case 3: this.pager = this.pagerService.getPager(this.total3, page, 10);
        this.getDataPage(page - 1);
        break;
    }
  }
}
